import React,{Component} from 'react'
import { Row, Col } from 'antd';

class Overview extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        const bg = require("biz-imgs/about/overviewBG.png");
        return (
            <div className="overviewFrame">
                <img src={bg} className="bg"/>
                <Row className="title">公司概况</Row>
                <p className="overviewContent">阳光融资坐落于上海浦东陆家嘴金融中心,秉承为客户创造价值的经营理念,以金融创新为宗旨,以供应链金融为核心,是一家多元化,综合性的金融服务企业,涵盖了互联网金融,商业保理,资产管理以及跨境金融等业务.以创新的专业化金融服务,为个人及机构投资者提供完善的理财服务,为进出口贸易,大宗交易及中小企业经营提供专业,高效的融资服务,创造价值.</p>
                <Row className="title">企业文化</Row>
                <ul className="culture">
                    <li className="subTitle">愿景
                        <p>创新金融 融通资本 成就财富</p>
                    </li>
                    <li className="subTitle">使命
                        <p>通过不断的金融创新,为客户创造更多价值</p>
                    </li>
                    <li className="subTitle">价值观
                        <p>诚信为本 专业领先 创新为魂</p>
                    </li>
                    <li className="subTitle">企业精神
                        <p>诚信 专业 创新</p>
                    </li>
                </ul>
            </div>
        )
    }
}

module.exports = Overview;


